<template>
	<div class="about-us">
		<div style="padding-top: 50px;text-align: center;">
			<img :src="logoImg" width="100" height="100"/>
			<p style="color: #66688F;font-size: 16px;">币多多生态钱包</p>
			<p style="color: #66688F;">MULTI-CURRENCY ECO -WALLET</p>
			<p style="color: #a5a5a5;padding: 50px;">DYLC钱包是一款全新的数字货币钱包为你提供安全、便捷的一站式管理方案</p>
			<p style="position: absolute;bottom: 30px;color: #a5a5a5;left: 0;right: 0;">当前版本1.0.0</p>
		</div>
	</div>
</template>

<script>
export default {
  name: 'AboutUs',
  data(){
  	return{
  		type:'',
  		logoImg:require('../../assets/img/logo.png')
  	}
  },
  methods:{
  	getHelpList(){
  		let _this = this;
  		_this.$http.get(_this.$lib.url + 'helping/lists?page='+_this.page).then(res => {
			console.log(res);
			if (res.status == 0){
				_this.refreshing = false;
				const _list = res.data.data;
		        _this.helpList = [...this.helpList, ..._list];
		        if(_list.length < 10){
		            /* 所有数据加载完毕 */
					_this.loading = false;
					_this.loadAll = true;
		            return;
		        }
			}
		}).catch(err => {})
  	},
  },
  mounted(){
  	this.page=1;
  	this.helpList=[];
//	this.getHelpList();
  },
  computed:{
//	
  },
  watch:{
  	$route(to,from){
  		
  	}
  }
 }
</script>

<style scoped>
.about-us{

}

</style>


